.triangle-3{border-top:12.5px solid transparent;border-right:75px solid #7D8584;border-bottom:50px solid transparent}@media only screen and (min-width: 992px){.triangle-3{border-top:18.75px solid transparent;border-right:112.5px solid #7D8584;border-bottom:75px solid transparent}}.triangle.rotate{-webkit-transform:rotate(-15deg);transform:rotate(-15deg)}.project{border-radius:2px;box-shadow:2px 1px 3px 2px lightgrey;display:block;margin-bottom:2em;position:relative}.project:hover .project-desc{background-color:#C65371}.project-img{padding:0.5em;width:100%;max-width:100%}.project-desc{background-color:rgba(198,83,113,0.85);border-radius:0 0 2px 2px;color:white;font-size:1.149em;font-weight:300;padding:0.5em;text-align:center;width:100%;position:absolute;bottom:0;-webkit-transition:0.25s;transition:0.25s}.project-desc p{margin:0}.project-title{font-size:1.32em;font-weight:400;margin:0;padding-top:0.5em;padding-bottom:0.5em;text-align:center}@media only screen and (min-width: 1200px){.project-title{font-size:1.515em}}.section-title{margin-top:2em;margin-bottom:2em;font-size:1.149em;text-align:center}@media only screen and (min-width: 992px){.section-title{font-size:1.32em}}.section-title h3{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.section-triangle{border-top:50px solid transparent;border-bottom:12.5px solid transparent;left:calc(50% - 75px);-webkit-transform:translate(-50%, 50%);transform:translate(-50%, 50%)}.tri-hotpink{border-right:75px solid #C65371}.tri-lightpink{border-right:75px solid #E8C7BE}.tri-gray{border-right:75px solid #7D8584}.about-me{background-color:#C65371;color:white;font-size:1.149em;font-weight:300}@media only screen and (min-width: 992px){.about-me{font-size:1.32em}}.about-me ul{-webkit-column-count:2;column-count:2}@media only screen and (min-width: 768px){.about-me ul{-webkit-column-count:3;column-count:3}}.about-title{font-size:1.5em;text-align:center}.fancy{font-family:"Bad Script",cursive}.contact-icons{text-align:center}.contact-icons a{display:inline-block;margin:1em;height:4em;width:4em}.contact-icons i{color:#7D8584;font-size:2.5em;-webkit-transition:all 0.25s;transition:all 0.25s}.contact-icons i:hover{color:#C65371;-webkit-transform:scale(1.5);transform:scale(1.5)}

